/* Add a black background color to the top navigation */
.header {
  display: flex;
  justify-content: space-between;
  background-color: #333;
  overflow: hidden;
  align-items: center;
}
.topnav {
  }

.auth {
  button {
    font-size: 15px;
    background-color: #04AA6D;
    color: white;
    border: none;
    border-radius: 30px;
    padding: 8px 10px;
    width: 110px;
  }
}
  
  /* Style the links inside the navigation bar */
  .topnav a {
    float: left;
    color: #f2f2f2;
    text-align: center;
    padding: 16px 16px;
    text-decoration: none;
    font-size: 17px;
  }
  
  /* Change the color of links on hover */
  .topnav a:hover {
    background-color: #ddd;
    color: black;
  }
  
  /* Add a color to the active/current link */
  .topnav a.active {
    background-color: #04AA6D;
    color: white;
  }